












import { defineComponent, computed } from '@nuxtjs/composition-api';
import { strToCharacters } from '@/utils';

export default defineComponent({
  props: {
    texts: {
      type: String,
      required: true
    }
  },
  setup(props) {
    const chars = computed(() => strToCharacters(props.texts).splice(1));
    return { chars };
  }
});
